<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
      <p><span class=rvts17></span></p>
<p class=rvps2><span class=rvts31></span>
<br><img width=1300 height=731 alt="" hspace=1 vspace=1 src="../../assets/pic1/abcamber19.jpg"><span class=rvts20></span>
<br><span class=rvts20>1.</span><span class=rvts21></span>
<br><span class=rvts20>2.</span><span class=rvts21></span>
<br><span class=rvts20>3.</span><span class=rvts21></span>
<br><span class=rvts20>4.</span><span class=rvts21></span>
<br><span class=rvts20>5.</span><span class=rvts21></span>
<br><span class=rvts20></span>
<br><span class=rvts32></span>
<br><img width=1300 height=731 alt="" hspace=1 vspace=1 src="../../assets/pic1/abcamber20.jpg"><span class=rvts20></span>
<br><span class=rvts21></span>
<br><span class=rvts20></span>
<br><span class=rvts32></span>
<br><img width=1300 height=731 alt="" hspace=1 vspace=1 src="../../assets/pic1/abcamber21.jpg"><span class=rvts20></span>
<br><span class=rvts20>1.</span><span class=rvts21></span>
<br><span class=rvts20>2.</span><span class=rvts21></span>
<br><span class=rvts20>3.</span><span class=rvts21></span>
<br><span class=rvts20></span>
<br><span class=rvts32></span>
<br><img width=1300 height=731 alt="" hspace=1 vspace=1 src="../../assets/pic1/abcamber22.jpg"><span class=rvts20></span>
<br><span class=rvts21></span>
<br><span class=rvts20></span>
<br><span class=rvts32></span>
<br><img width=1300 height=731 alt="" hspace=1 vspace=1 src="../../assets/pic1/abcamber23.jpg"><span class=rvts20></span>
<br><span class=rvts21></span><span class=rvts20>,</span><span class=rvts21></span>
<br><span class=rvts20></span>
<br><span class=rvts32></span>
<br><img width=1300 height=731 alt="" hspace=1 vspace=1 src="../../assets/pic1/abcamber24.jpg"><span class=rvts20></span>
<br><span class=rvts21></span></p>
<p><br></p>
<p><br></p>
    </div>
  </div>
</template>

<script>
import BScroll from "better-scroll";
export default {
  data() {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>

<style lang="stylus" type="stylesheet/stylus" scoped>
@import '../../common/stylus/mixin.styl';

.page {
  overflow: hidden;
  height: 100%;
  position: absolute;
  left: 0;
  right: 0;

  .content {
    margin-top: -0.1rem;
    margin-bottom: 0.2rem;
    height: 14rem;
    .rvps2 {
      text-align: justify;
    }
    .rvts17 {
      font-size: 14pt;
      font-family: '';
      font-weight: bold;
      color: #1f497d;
    }

    .rvts20 {
      font-size: 12pt;
      font-family: 'Tahoma', 'Geneva', sans-serif;
    }

    .rvts21 {
      font-size: 12pt;
      font-family: '';
    }
    .rvts31{
      font-size: 14pt;
      font-family: '';
      font-weight: bold;
      color: #c808bf;
    }
    .rvts32{
      font-size: 12pt;
      font-family: '';
      font-weight: bold;
      color: #c808bf;
    }
  }
}
</style>



